.modules-page {
  padding: 30px;
  font-family: "Segoe UI", sans-serif;
  background-color: #f9f9f9;
  min-height: 100vh;
}

.modules-header {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.modules-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.modules-toolbar input,
.modules-toolbar select {
  padding: 8px 12px;
  margin-right: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.modules-toolbar button {
  background-color: #3465b5;
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.modules-toolbar button:hover {
  background-color: #274a90;
}

.stats-box {
  display: flex;
  gap: 20px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}

.stat {
  background-color: white;
  padding: 15px 25px;
  border-radius: 8px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  flex: 1;
  min-width: 150px;
}

.stat-title {
  font-size: 14px;
  color: #888;
}

.stat-number {
  font-size: 22px;
  font-weight: bold;
  color: #333;
}

.modules-table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  overflow: hidden;
}

.modules-table th,
.modules-table td {
  padding: 12px 16px;
  border-bottom: 1px solid #eee;
  text-align: left;
  font-size: 14px;
}

.modules-table th {
  background-color: #f0f2f5;
  font-weight: 600;
}

.modules-table tr:hover {
  background-color: #f9f9f9;
}

.action-buttons {
  display: flex;
  gap: 10px;
}

.action-buttons button {
  padding: 6px 10px;
  font-size: 13px;
  border-radius: 4px;
  cursor: pointer;
  border: none;
}

.edit-btn {
  background-color: #4caf50;
  color: white;
}

.remove-btn {
  background-color: #f44336;
  color: white;
}

.details-btn {
  background-color: #2196f3;
  color: white;
}
